@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,800;1,300&display=swap');
*{
	padding: 0;
	margin: 0;
}
body{
	font-family: 'montserrat', sans-serif;
	font-weight: 300;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}
.header{
	display: flex;
	flex-direction: row;
	align-items: center;
	background-color: $bg-color;
	&__logo{
		margin: 5px 5px 5px 80px;
		border-radius: 50px;
		&--img{
			border-radius: 50px;
			width: 50px;
		}
	}
	&__botonera{
		list-style: none;
		flex-grow: 1;
		text-align: center;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		margin-right: 80px;
		&--menu{
			color: black;
			text-decoration: none;
			font-size: 1.3rem;
			font-weight: bold;
		}		
	}
}
.banner{
	background-image: url('../media/drone.jpg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	min-height: 50vh;
	margin: 50px 0;
	&__titulos{
		font-weight: bold;
		font-size: 3rem;
		text-align: center;
		margin: 15px;
		&--subTitulos{
			font-weight: italic;
			font-size: 2rem;
			text-align: center;
		}
	}
}
.mensajes{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
	justify-content: space-around;
	margin-top: 50px;
	height: 300px;
	&__contenedores{
		background-color: #bafb8d;
		height: 200px;
		width: 300px;
		border-radius: 60px;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
	&__icon{
		font-size: 3rem;
		color: $color1;
		text-align: center;
	}
	&__titulos{
			font-size: 3rem;
			text-align: center;
			padding: 5px;
			color: $color1;
			&--descripcion{
				font-size: 1rem;
				text-align: center;
				color: $color2;
			}
		}
}
.info{
	display: flex;
	flex-direction: row;
	background-color: $bg-color;
	width: 100%;
	justify-content: center;
	margin-bottom: 50px;
	min-height: 150px;
	align-content: center;
	&__titulos{
		font-weight: bold;
		color: $color1;
		font-size: 3rem;
		text-align: center;
		margin: 30px 50px 0 30px;
		&--datos{
			font-weight: normal;
			color: $color2;
			font-size: 1rem;
			text-align: center;
			padding: 5px 50px 30px 30px;
		}
		&--lineas{
			border-right: 2px solid white;
			margin: 30px 0;
		}
	}
}
.footer{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	min-height: 100px;
	align-items: center;
	justify-content: flex-start;
	background-color: $bg-color;
	&__texto{
		color: $color1;
		font-size: 1.2rem;
		margin: 0 20px;
		&--redes{
			width: 50px;
			height: 50px;
			transition: height 1s, width 1s;
				&:hover{
				height: 60px;
				width: 60px;
			}
		}
		&--copy{
			display: flex;
			flex-direction: row;
			justify-content: flex-end;
			font-size: 1rem;
			flex-grow: 1;
			color: $color2;
		}
	}
}
.presentacion1{
	min-height: 500px;
	display: flex;
	flex-direction: row;
	margin: 50px 20px;
	&__texto{
		max-width: 50%;
		min-width: 585px;
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		justify-content: flex-start;
		font-size: 1.5rem;
		text-align: justify;
		margin-right: 20px;
		&--nombre{
			font-size: 2.8rem;
			padding-bottom: 15px;
		}
		&--detalle{
			padding-bottom: 50px;
			font-size: 1.7rem;
		}
	}
	&__fondo{
		background-image: url('../media/pench.jpg');
		width: 50%;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
	}	
}
